import React from "react";
import { BrowserRouter } from "react-router-dom";
import App from "../shared/App";

const Root = () => (
  // 404.html 사용하기 이전
  // <BrowserRouter basename={process.env.PUBLIC_URL}>
  <BrowserRouter basename="/pyp">
    <App></App>
  </BrowserRouter>
);

export default Root;
